.search-container {
  margin: 0 auto;
  padding: 0 20px;
  max-width: 600px;
  height: 300px;
  position: relative;
  z-index: 3; }
  .search-container label {
    display: block;
    padding-left: 10px;
    padding-bottom: 5px;
    font-weight: 300;
    font-size: 1.1em; }
  .search-container .input-row {
    display: grid;
    grid-template-columns: 1fr auto;
    align-items: center; }
    .search-container .input-row #search-box {
      outline: none;
      border: none;
      background: #111821;
      color: white;
      padding: 15px 25px;
      font-size: 1.1em;
      border-radius: 10px 0 0 10px;
      font-weight: 300;
      width: 100%;
      box-sizing: border-box; }
    .search-container .input-row .added-tags {
      min-width: 10px;
      padding-right: 5px;
      height: 100%;
      background: #111821;
      border-radius: 0 10px 10px 0;
      display: flex;
      align-items: center; }
      .search-container .input-row .added-tags > span {
        font-size: 0.7em;
        border: 1px solid white;
        border-radius: 4px;
        margin: 0 5px;
        padding: 5px;
        padding-left: 10px;
        display: grid;
        grid-template-columns: 1fr auto;
        align-items: center; }
        .search-container .input-row .added-tags > span .remove-btn {
          display: inline-block;
          padding: 5px;
          margin-left: 3px;
          height: 8px;
          width: 8px;
          border-radius: 50%;
          display: flex;
          justify-content: center;
          align-items: center; }
          .search-container .input-row .added-tags > span .remove-btn:hover {
            background: #fff2;
            cursor: pointer; }
      .search-container .input-row .added-tags .leftovers {
        padding: 5px;
        background: white;
        color: black; }
  .search-container .tags-container {
    background: #111821;
    width: 100%;
    box-sizing: border-box;
    overflow: hidden;
    margin-top: -10px;
    max-height: 0;
    transition: 0.25s; }
    .search-container .tags-container.open {
      max-height: 200px;
      overflow-y: auto;
      padding-top: 10px; }
    .search-container .tags-container > div {
      box-sizing: border-box;
      padding: 10px 20px; }
      .search-container .tags-container > div:hover {
        background: #0d131a;
        cursor: pointer; }

@media screen and (max-width: 700px) {
  .search-container {
    height: 150px; } }
